header {
    width: 99.6%;
    height: 500px;
    background-size: cover;
    background-repeat: no-repeat;
    background-image: url("../assets/lieux_header.png");
    border: 5px black solid;
    margin-bottom: 10px;
    font-size: 23px;
}

.header-location-m a {
    color: white;
    text-decoration: none;
    text-shadow: 10px;
}

.header-link-m:hover {
    border: 3px solid goldenrod;
    background-color: violet;
    border-radius: 100%;
    color: black;
    margin-left: 15px;
}

a.header-link-m {
    z-index: 15;
    padding: 25px;
    margin-left: 40px;
}

body {
    font-family: "Rock Salt";
    font-weight: bold;
    cursor: pointer;
    
}

main {
    width: 99.6%;
    border: 5px black solid;
    height: 100%;
    font-size: 15px;
}

.container-m {
    display: flex;
    flex-direction: column;
    padding: 5%;
    background-color: #A8D5EA;
    background: 
    linear-gradient(217deg, rgb(168, 213, 234), rgba(255,0,0,0) 70.71%),
    linear-gradient(127deg, rgba(0,255,0,.8), rgba(0,255,0,0) 70.71%),
    linear-gradient(286deg, rgba(255,0,0,.8), rgba(0,0,255,0) 70.71%);
}

.container-location-m {
    cursor: pointer;
}

.residents-m {
    display: none;
}

.hide-m {
    display: none;
}

.show-m {
    display: block;
}

